.header{
    box-shadow: 1px 1px 5px 0px #d6d6d6;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 200;
}
.header .first-row{
    background-color: #F4F4F5;
    padding: 10px 20px;
}
.header .first-row .container p{
    margin: 0 !important;
}
.logo img{
    object-fit: contain;
}
.sm-icon img{
    margin: 0 5px;
    margin-bottom: -3px;
}
.search-form{
    border: 1px solid #000;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.search-form input{
    border: none;
    background: none;
    font-size: 16px;
    outline: none;
    width: 100%;
}
.search-btn{
    background: none;
    border: none;
}
.header__icon img{
    object-fit: cover;
    margin: 0 10px;
}
.menu-row{
    width: 23px;
    padding: .5px;
    background-color: #000;
    margin-bottom: 7px;
}
.menu-row:last-child{
    margin-bottom: 0;
}
.nav{
    padding: 15px 30px;
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    background-color: #fff;
}
.nav .col-4{
    padding: 0;
}
.menu-btn{
    border: none;
    background: none;
    display: none;
}
.menu{
    padding: 0;
}
.logo a img{
    object-fit: cover;
}
.icons{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.icons button{
    border: none;
    background: none;
}

.inner-menu{
    background-color: rgba(0, 0, 0, 0.85);
    width: 100%;
    height: 90vh;
    position: fixed;
    top: 140px;
    display: none;
    z-index: 20;
    overflow: hidden;
}
.catalog-list{
    background-color: #fff;
    height: 100%;
    padding: 10px 20px;
}
.catalog-list li{
    background-color: #fff;
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 1px solid #bbbbbb;
    color: #000;
}
.catalog-list li a{
    color: #000;
}
.catalog-list li a:hover{
    color: #333;
}
.active{
    display: block !important;
}
.close-btn{
    background: none;
    border: none;
    display: none;
}
.search-block{
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0, .8);
    position: fixed;
    padding: 10px 0;
    display: none;
    z-index: 20;
    margin-top: 60px;
}
.search-form{
    background-color: #fff;
}
.second-row{
    padding: 20px;
    z-index: 20;
    background-color: #fff;
}