.product-list{
    width: 100%;
    margin: 0 auto;
    flex-wrap: wrap;
    padding: 0;
    justify-content: center;
}
.product{
    margin: 10px 20px;
    border: 2px solid #f4f4f4;
    max-width: 260px;
    min-width: 200px;
    position: relative;
}
.like_btn{
    background: none;
    border: none;
    position: absolute;
    top: 10px;
    right: 10px;
}
.like-icon{
    background-color: transparent;
    overflow: hidden;
}